}

.backArrow {
  position: absolute;
  top: 80%;
  right: 51%;
  z-index: 999;
  color: rgb(77, 77, 77);
}

.fab {
  margin-left: 0 15px;
  font-size: 25px;
  size: 40px;
  height: 30px;
  cursor: pointer;
}
.fab:hover {
  color: #d87843;
}

@media (max-width: 700px){
  .nextArrow {
    left: 57%;
  }
}

.backArrow {
  left: 45%;
}
